body { margin: 0 }
.zoomable { cursor: crosshair }
.zoom {
    z-index: 1;
    cursor: zoom-in;
    position: absolute;
    background-color: #888;
    mix-blend-mode: color-dodge;
    border: 1px dashed white;
}

#slider {
    background-color: #333;
    mix-blend-mode: color-dodge;
    height: 100%;
    width: 10%;
    position: absolute;
    top: 0;
    left: 90%;
    display: none;
}

#slider .up, #slider .down {
    height: 50%;
}

form {
    display: block;
    position: absolute;
}

@media only screen and (max-device-width: 736px) {
    #slider {display: block;}
    form {display: none; }
}
